import React from "react";
import pf from "petfinder-client";
import Pet from "./Pet";
import Styled from "styled-components";

const petfinder = pf({
  key: process.env.API_KEY,
  secret: process.env.API_SECRET
});

class Results extends React.Component {
  state = {
    pets: []
  };

  componentDidMount() {
    petfinder.pet.find({ find: "full", location: "Seattle, WA" }).then(data => {
      let pets;
      if (data.petfinder.pets && data.petfinder.pets.pet) {
        //if it exists...
        if (Array.isArray(data.petfinder.pets.pet)) {
          // if multiple pets
          pets = data.petfinder.pets.pet;
        } else {
          //if just one, wrap it in an array
          pets = [data.petfinder.pets.pet];
        }
      } else {
        pets = [];
      }

      this.setState({ pets });
    });
  }

  render() {
    return (
      <div className="search">
        {this.state.pets.map((pet, idx) => {
          // often in this API dogs can be more than one breed, therefore necessitating these clauses below
          let breed;
          if (Array.isArray(pet.breeds.breed)) {
            breed = pet.breeds.breed.join(", ");
          } else {
            breed = pet.breeds.breed;
          }
          return (
            <Pet
              key={idx}
              media={pet.media}
              animal={pet.animal}
              name={pet.name}
              breed={breed}
              location={`${pet.contact.city}, ${pet.contact.state}`}
              id={pet.id}
            />
          );
        })}
      </div>
    );
  }
}

export default Results;
